<template>
  <div class="menu-buttons">
    <a href="#about">
      <div :class="get_actual_cl_1">
        <v-icon class="menu-button-icon" name="info"/>
        <div class="button-name">o mnie</div>
      </div>
    </a>
    <a href="#portfolio">
      <div :class="get_actual_cl_2">
        <v-icon class="menu-button-icon" name="airplay"/>
        <div class="button-name">projekty</div>
      </div>
    </a>
    <a href="#contact">
      <div :class="get_actual_cl_3">
        <v-icon class="menu-button-icon" name="smartphone"/>
        <div class="button-name">kontakt</div>
      </div>
    </a>
  </div>
</template>

<script>
export default {
    name: "MenuButtons",
    data: function () {
        return {
            actual_class_1: "button",
            actual_class_2: "button br-top",
            actual_class_3: "button br-top",
            about: 0,
            header: 0,
            portfolio: 0,
        }
    },
    mounted() {
        window.addEventListener('scroll', this.set_cl);
        window.addEventListener('resize', this.resize);
        this.resize();
        this.set_cl();
    },
    computed: {
        get_actual_cl_1() {
            return this.actual_class_1;
        },
        get_actual_cl_2() {
            return this.actual_class_2;
        },
        get_actual_cl_3() {
            return this.actual_class_3;
        },
    },
    methods: {
        resize() {
            this.about = document.getElementById('about').scrollHeight;
            this.header = document.getElementById('header').scrollHeight;
            this.portfolio = document.getElementById('portfolio').scrollHeight;
        },
        set_cl() {
            this.actual_class_1 = "button";
            this.actual_class_2 = "button br-top";
            this.actual_class_3 = "button br-top";

            const first_page = this.about
            const second_page = first_page + this.portfolio

            if (window.scrollY <= first_page) {
                this.actual_class_1 = "button chosen";
            } else if (window.scrollY > first_page && window.scrollY <= second_page) {
                this.actual_class_2 = "button chosen br-top";
            } else if (window.scrollY > second_page) {
                this.actual_class_3 = "button chosen br-top";
            }
        },
    },
}
</script>

