<template>
  <div>
    <Header/>
    <SideBar :clicked_menu="get_clicked_menu"/>
  </div>
</template>
<script>
import Header from "../organism/Header";
import SideBar from "../organism/SideBar";
import 'roboto-fontface/css/roboto/roboto-fontface.css'

export default {
    name: 'Page',
    components: {Header, SideBar,},
    data: function () {
        return {
            clicked_menu: false,
        };
    },
    mounted() {
        this.$on("sidebar_clicked", this.sidebar_clicked);
    },
    computed: {
        get_clicked_menu() {
            return this.clicked_menu;
        },
    },
    methods: {
        sidebar_clicked() {
            this.clicked_menu = !this.clicked_menu;
        },
    },
}
</script>